.button {
  cursor: pointer;
  appearance: none;
  outline: none !important;
  background-color: transparent;
  box-shadow: none;
  border: 0;
  padding: 0;
  border-radius: 0;
  position: relative;
  text-decoration: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1.375rem;
  transition: color $trs1, border-color $trs1, background-color $trs1;
  height: 64px;
  line-height: 60px;
  padding: 0 25px;
  &_style-1 {
    background-color: $color-accent;
    border: 2px solid $color-accent;
    color: #fff;
    letter-spacing: 1px;
    &:hover {
      background-color: $color-accent-hover;
      border-color: $color-accent-hover;
    }
  }
  &_style-2 {
    color: $color-accent;
    border: 2px solid transparent;
    &:hover {
      background-color: rgba(97, 184, 124, 0.05);
      border-color: $color-accent;
    }
  }
  &_small {
    line-height: 32px;
    height: 36px;
    font-size: 1rem;
    letter-spacing: normal;
    padding: 0 15px;
  }
}